.App {
  text-align: center;
}
h1{
  text-align: center;
}
body {
  background-color: #4f8b72;
}
nav{
  height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color:#f5c15e;
}
a{
  color: rgb(0, 0, 0);
}
.social-container {
  background: #eee;
  padding: 25px 50px;
}
a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}
a.social:hover {
  transform: translateY(-2px);
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}

h5{
  font-family:Impact, fantasy;
  font-size: 1.8vmax;
}